.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.My-header{
  display: flex;
  align-items: center;
  justify-content: center;
  color: cornflowerblue;
  /* background-color: beige; */
}

.Main-box{
  margin-top: 80px;
  display: flex;
  /* width: 200px; */
  align-items: center;
  justify-content: center;
  /* max-width: 800px; */
}

.Box1{
  display: flex;
  /* flex-wrap: wrap; */
  flex-direction: column;
  border: .5px solid #808080;
  padding: 25px;
  margin: 20px;
  justify-content: center;
  /* justify-content: space-around; */
  align-items: center;
  /* align-items: normal; */
  width: 100%;
}

.Input-file{
  display: block;
  /* border: 1px solid #ccc; */
  /* cursor: pointer; */
  margin-bottom: 10px;
  /* height: 10px; */
  color: cornflowerblue;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  /* background-color: #f0f0f0; */
}

.flex-item{
  display: block;
  justify-content: center;
  align-items: center;
  /* max-width: 800px; */
  flex-grow: 1;
  /* width: 100%; */
}
.Text-block{
  width: 100%;
}

.My-radio{
  display: inline-block;
  margin:25px;
  height: 20px;
}

.My-radio input[type='radio']:checked + label{
  font-weight: bold;
  color:blueviolet;
}

/* .My-radio-selected{
  display: inline-block;
  margin:25px;
  height: 10px;

  
} */

.Text-area{
  width: 100%;
  /* max-width: 600px; */
  min-height: 150px;
  overflow: auto;
  resize: none;
}

.App-link {
  color:#80bfff;
}

.My-button {
  margin-top: 20px;
  width: 100px;
  background-color: cornflowerblue; /* 背景颜色 */
  color: #ffffff; /* 文本颜色 */
  padding: 10px 15px; /* 内边距 */
  border: none; /* 去掉边框 */
  border-radius: 5px; /* 圆角边框 */
  cursor: pointer; /* 鼠标样式 */
  font-size: 13px; /* 文本大小 */
  transition: background-color 0.3s; /* 添加过渡效果 */
}

/* 悬停时的样式 */
.My-button:hover {
  background-color: rgb(67, 126, 238);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.My-body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* max-width: 800px; */
  /* max-width: 850px; */
}

#root{
  display: block;
  /* flex-direction: column; */
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 900px;
}

.Text-content{
  font-size:18px;
}
.AI-warn{
  /* display: inline-block; */
  /* width: 100%; */
  color:red;
  font-style: italic;
  font-size: small;
  font-family: Arial, Helvetica, sans-serif;
